@import url("https://fonts.googleapis.com/css2?family=Poor+Story&display=swap");
:root {
  --font-primary: "Poor Story", cursive;

  --clr-primary: hsl(0, 0%, 0%);
  --clr-secondary: hsl(210, 54%, 5%);
  --clr-lamp: hsl(51, 100%, 39%);
  --clr-font: rgb(196, 196, 196);
  --clr-black: black;

  --linear-gradient01: linear-gradient(rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.1));
  --linear-gradient02: linear-gradient(rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 02));
  --linear-gradient03: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3));
  --linear-gradient04: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4));
  --linear-gradient05: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5));
  --linear-gradient06: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6));
  --linear-gradient07: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7));
  --linear-gradient08: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8));
  --linear-gradient09: linear-gradient(rgba(0, 0, 0, 0.9), rgba(0, 0, 0, 0.9));

  --padding-btn: 0.5em;
  --padding-div: 1em;
  --padding-input: 0.3em;
  --padding-section: 2.5em;

  --margin-div: 1em;
  --margin-input: 0.5em;

  --border-btn: 0.2rem solid var(--clr-font);
  --border-div: 0.2rem solid var(--clr-font);
  --border-input: 0.2rem solid var(--clr-font);

  --shadow-div: 0 0 1em 0.5em var(--clr-primary);

  --transition02: all 0.2s linear;
}
html {
  scroll-behavior: smooth;
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  font-weight: 900;
  color: var(--clr-font);
  font-family: var(--font-primary);
  background-color: var(--clr-secondary);
  /* background */
  background: var(--linear-gradient03),
    url("./assets/images/logo.jpg") center/cover no-repeat;
  background-attachment: fixed;
  min-height: 100vh;
}
@media screen and (max-width: 500px) {
  body {
    background: var(--clr-primary);
  }
}
h1 {
  font-size: 1.6rem;
}
h2 {
  font-size: 1.4rem;
}
h3 {
  font-size: 1.2rem;
}
h4 {
  font-size: 1rem;
}
h5 {
  font-size: 0.8rem;
}
p {
  font-size: 0.9rem;
}
.height-100 {
  min-height: 100vh;
}
.height-90 {
  min-height: 90vh;
}
.height-80 {
  min-height: 80vh;
}
.height-20 {
  min-height: 20vh;
}
.center-flex {
  display: flex;
  justify-content: center;
  align-items: center;
}
.primary-btn {
  background-color: transparent;
  padding: var(--padding-btn);
  border: var(--border-btn);
  color: var(--clr-font);
  font-size: 1.4rem;
  font-weight: 700;
  font-family: var(--font-primary);
}
.primary-btn:hover {
  border-color: var(--clr-lamp);
  color: var(--clr-lamp);
}
@media screen and (min-width: 768px) {
  h1 {
    font-size: 1.9rem;
  }
  h2 {
    font-size: 1.7rem;
  }
  h3 {
    font-size: 1.5rem;
  }
  h4 {
    font-size: 1.3rem;
  }
  h5 {
    font-size: 1.1rem;
  }
  p {
    font-size: 1.1rem;
  }
}
@media screen and (min-width: 1180px) {
  h1 {
    font-size: 2.1rem;
  }
  h2 {
    font-size: 1.9rem;
  }
  h3 {
    font-size: 1.7rem;
  }
  h4 {
    font-size: 1.5rem;
  }
  h5 {
    font-size: 1.3rem;
  }
  p {
    font-size: 1.3rem;
  }
}
/* HOME PAGE*/
/* LOGO SECTION */
#user-name {
  z-index: 999;
  position: fixed;
  bottom: 0;
  width: 100%;
  padding: var(--padding-div);

  text-align: end;
  color: var(--clr-lamp);
}
#user-name span {
  font-size: 0.8em;
}
.logo-section {
  width: 100%;
  position: relative;
  flex-direction: column;

  background-color: var(--clr-primary);
  box-shadow: var(--shadow-div);
}
.video-login-container {
  width: 100%;
  flex-direction: column;
}
.img-container {
  margin: var(--margin-div);
  color: var(--clr-lamp);
}
.img-container h2 {
  cursor: pointer;
}
.logo-section a {
  text-decoration: none;
  color: var(--clr-lamp);
  margin: var(--margin-input);
  font-size: 2em;
  transition: var(--transition02);
  transform: scale(1);
}
.logo-section a:hover {
  transform: scale(1.2);
}
.logo-section .video-container {
  height: 200px;
  width: 90%;
  max-width: 300px;
  border: var(--border-div);
  margin: var(--margin-div);
}
.iframe-video {
  width: 100%;
  height: 100%;
}
/* menu btn added 18.11.2022 */
.phone-menu-btn-container {
  display: none;
  width: 100%;
  z-index: 999;
  position: absolute;
  top: 70%;
  right: 0;
  transition: var(--transition02);
}
#phone-menu-btn {
  flex-direction: column;
  height: 5em;
  width: 4em;
  background: var(--clr-primary);
}
#phone-menu-btn button {
  height: 50%;
  font-size: 1.6em;
  width: auto;
  background-color: transparent;
  border: var(--border-btn);
  color: var(--clr-lamp);
  font-weight: 900;
  border-color: var(--clr-lamp);
}
#phone-menu-btn #menu-switch-btn {
  transform: rotate(0);
  transition: var(--transition02);
}
#menu-switch-icon {
  transition: var(--transition02);
}
.icon-left {
  transform: rotate(180deg);
}
@media screen and (max-width: 500px) {
  #phone-menu-btn-container {
    display: block;
  }
  #phone-menu-btn {
    display: flex;
  }
}
.logo-section-right {
  position: relative;
  height: 150px;
  width: 90%;
  max-width: 300px;
  flex-direction: column;
  margin: var(--margin-div);
}
/* LOGIN BUTTON */
.login-btn {
  z-index: 999;
  cursor: pointer;
  height: 50%;
  min-width: 120px;

  font-size: 1.5rem;
  font-weight: 700;
  text-align: center;
  padding: var(--padding-btn);
  border: 0.2rem solid var(--clr-font);
  color: var(--clr-font);
}
.login-btn:hover {
  color: var(--clr-lamp);
  border-color: var(--clr-lamp);
}
/* LANGUAGE SELECTOR */
.language-selector {
  height: 50%;
  padding: 0.1em;
  margin: var(--margin-div);
}
.language-selector span {
  cursor: pointer;
  padding: 0.1em;
  margin: 0.2em;
  font-size: 1.4rem;
}
.span-active {
  color: var(--clr-lamp);
}
@media screen and (min-width: 500px) {
  .video-login-container {
    flex-direction: row;
  }
  .login-btn,
  .primary-btn {
    font-size: 1.7rem;
  }
  .language-selector {
    margin-top: 0;
  }
  .language-selector span {
    font-size: 1.6rem;
  }
}
@media screen and (min-width: 1000px) {
  .logo-section {
    flex-direction: row;
    justify-content: space-between;
    min-height: 220px;
  }
  .logo-section .video-login-container {
    width: auto;
  }
  .logo-section .video-container {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    margin: 0;
  }
  .login-btn {
    position: absolute;
    right: 0;
    bottom: 0;
    font-size: 1.9rem;
  }
  .primary-btn {
    font-size: 1.9rem;
  }
  .language-selector {
    position: absolute;
    right: 0;
    top: 0;
    margin-top: 0;
  }
}
@media screen and (min-width: 1180px) {
  .language-selector span {
    font-size: 1.8rem;
  }
}
/*BODY SECTION */
.body-section {
  padding: var(--padding-section);
  width: fit-content;
  display: grid;
  position: relative;
}
.drop-section-container {
  background-color: var(--clr-primary);
  margin: 2em 0em 2em 0em;
  height: fit-content;
}
.drop-section-container h3 {
  padding: 0.5em;
}
.drop-title {
  cursor: pointer;
}
.drop-section {
  display: flex;
  flex-direction: column;
  height: 0;
  overflow: hidden;
  width: 100%;
  transition: var(--transition02);
}
.drop-section-show {
  background-color: var(--clr-primary);
  z-index: 1001;
}
.drop-link {
  text-decoration: none;
  color: var(--font-primary);
  transition: var(--transition02);
  border-top: 0.15rem solid var(--clr-font);
}
.drop-link:hover {
  color: var(--clr-lamp);
}
@media screen and (max-width: 500px) {
  .body-section {
    z-index: 999;
    position: fixed;
    top: 15%;
    left: 4em;
    overflow-y: scroll;
    width: calc(100vw - 8em);
    height: 70%;

    padding: var(--padding-div);
    background: var(--clr-primary);
    border: var(--border-div);
    border-color: var(--clr-lamp);
    transform: scale(0);
  }
  .body-section-show {
    transform-origin: center;
    transform: scale(1);
    transition: var(--transition02);
  }
  .body-section h3 {
    text-align: center;
  }
  .drop-section-container h3 {
    border-bottom: 0.15rem solid var(--clr-font);
    border-color: var(--clr-lamp);
  }
  .drop-link {
    border: none;
  }
}
@media screen and (min-width: 1080px) {
  .body-section {
    width: 100%;
    justify-content: space-around;
    grid-template-columns: repeat(4, auto);
  }
  .drop-section-container {
    width: 200px;
    text-align: center;
  }
}
/* AUTHENTICATION PAGE */
#auth-section {
  flex-direction: column;
}
.auth-body {
  flex-direction: column;
}
.auth-body::after {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: var(--linear-gradient08);
}
.form-container {
  z-index: 999;
  padding: var(--padding-div);
  margin: var(--margin-div);
  width: 100%;
}
#auth-section form {
  flex-direction: column;
}
#auth-section form input {
  width: 90%;
  max-width: 350px;
  padding: var(--padding-input);
  margin: var(--margin-input);
}
#auth-section form button {
  margin: 2.5em 0 0 0;
}
.text-container {
  z-index: 999;
  padding: var(--padding-div);
  margin: var(--margin-div);
}
.text-container h1 {
  max-width: 550px;
}
.auth-error-container {
  z-index: 999;
  width: 80%;
  max-width: 530px;
  text-align: center;
}
#auth-section .img-container {
  z-index: 999;
}
@media screen and (max-width: 500px) {
  #auth-section {
    background: url("./assets/images/logo.jpg") center/cover no-repeat;
  }
}
@media screen and (min-width: 1080px) {
  #auth-section {
    width: 50%;
    position: relative;
  }
  .auth-body::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    min-height: 100vh;
  }
}

/* FINISHED PROJECTS PAGE */
#finished-projects {
  padding: var(--padding-section);
  background: var(--linear-gradient05),
    url("./assets/images/finished_projects_2.jpg") center/cover no-repeat;
  background-attachment: fixed;
}
.projects-container {
  columns: 1;
  column-gap: 1em;
}
.project-item-container {
  cursor: pointer;
  max-width: 350px;
  flex-direction: column;
  overflow-y: auto;

  margin: 0 auto 2em auto;
  padding: var(--padding-div);
  background-color: var(--clr-primary);
  transition: var(--transition02);

  text-align: center;
}
.project-item-title {
  color: var(--clr-lamp);
}
.project-item-link {
  text-decoration: none;
  color: var(--font-primary);
}
@media screen and (min-width: 1000px) {
  .projects-container {
    columns: 2;
  }
  .project-item-container {
    opacity: 0.8;
  }
  .project-item-container:hover {
    opacity: 1;
  }
}
@media screen and (min-width: 1400px) {
  .projects-container {
    columns: 3;
  }
}
/* SHAREDFOOTER PAGE */
#shared-footer {
  position: relative;
  bottom: 0;
  max-width: 250px;
  margin: auto;
  text-align: center;
  padding: var(--padding-div);
}
